html {
    height: 100%;
}

body {
    width: 1920px;
    height: 1080px;
    color: #fff;
    font-family: '微软雅黑';
    background: url(images/BG.png) no-repeat;
    background-size: 100% 100%;
}

li {
    list-style: none;
}

.car-con {
    width: 1920px;
    height: 1080px;
}

.title {
    width: 1920px;
    height: 70px;
    background: url(images/title.png) 0 0/ 100% 100%;
}


/*主要内容*/

.container {
    width: 1880px;
    height: 945px;
    margin: 0 auto;
}

.top-con {
    width: 1880px;
    height: 490px;
    border: 1px solid red;
}

.bottom-con {
    width: 1880px;
    height: 405px;
    margin-top:40px;
    margin-top: 40px;
}


/*1---车辆总数*/

.car-total {
    width: 24%;
    height: 490px;
    background: url(images/div1.png) 0 0 /100% 100%;
    position: relative;
    float: left;
}

.car-title {
    width: 190px;
    height: 80px;
}

.text-icon {
    width: 86px;
    height: 80px;
    background: url(images/textbg.png) 0 0 /100% 100%;
    display: inline-block;
}

.car-text {
    font-size: 48px;
    color: #f6ca58;
    float: right;
    padding-top: 10px;
}

.car-list {
    width: 422px;
    height: 340px;
    position: absolute;
    top: 70px;
    left: 34px;
}

.car-bg {
    width: 416px;
    height: 72px;
    font-size: 24px;
    color: #5e8fff;
    background: url(images/mathBg.png) 0 0 /100% 100%;
    position: relative;
    margin-top: 18px;
}

.car-name {
    position: absolute;
    top: 20px;
    left: 18px;
}

.car-value {
    font-size: 40px;
    color: #fff;
    letter-spacing: 2px;
    position: absolute;
    top: 10px;
    left: 220px;
}

.car-unit {
    position: absolute;
    top: 28px;
    left: 350px;
}

.car-bar {
    width: 430px;
    height: 340px;
    position: absolute;
    top: 70px;
    left: 34px;
}


/*汽车品牌分布*/

.car-card {
    width: 24%;
    height: 490px;
    float: left;
    margin-left: 60px;
    background: url(images/div2.png) 0 0/100% 100%;
    position: relative;
}

.card-title {
    width: 240px;
    height: 54px;
    background: url(images/title-bg.png) 0 0/100% 100%;
    position: absolute;
    top: 30px;
    left: 15px;
}

.icon1 {
    width: 25px;
    height: 25px;
    background: url(images/icon1.png) 0 0/100% 100%;
    position: absolute;
    top: 12px;
    left: 30px;
}

.text {
    font-size: 18px;
    color: #fff;
    position: absolute;
    top: 15px;
    left: 70px;
}

.card-con {
    width: 400px;
    height: 400px;
    position: absolute;
    top: 70px;
    left: 35px;
}


/*车辆注册地分布*/

.car-register {
    width: 48%;
    height: 490px;
    float: right;
    position: relative;
    background: url(images/div3.png) 0 0/ 100% 100%;
}

.register-con {
    width: 812px;
    height: 340px;
    margin: 100px auto;
}

.icon2 {
    width: 25px;
    height: 25px;
    background: url(images/icon2.png) 0 0/100% 100%;
    position: absolute;
    top: 12px;
    left: 30px;
}


/*坐标轴*/

.x-mark path {
    fill: none;
    stroke: #011791;
    stroke-width: 2px;
}

.x-mark line {
    fill: none;
}

.x-mark text {
    font-size: 20px;
    font-family: Microsoft YaHei;
    fill: #a4d5ff;
}


/*车辆使用年限*/

.car-life {
    width: 557px;
    height: 430px;
    background: url(images/div4.png) 0 0/100% 100%;
    position: relative;
    display: inline-block;
}

.icon3 {
    width: 25px;
    height: 25px;
    background: url(images/icon3.png) 0 0/100% 100%;
    position: absolute;
    top: 12px;
    left: 30px;
}

.carlift-con {
    width: 475px;
    height: 257px;
    margin: 0 auto;
    position: absolute;
    top: 90px;
    left: 25px;
}

.carlife-bg {
    width: 400px;
    height: 257px;
    position: absolute;
    top: 124px;
    left: 120px;
}

.car-li {
    width: 370px;
    height: 32px;
    background: url(images/rectbg.png) 0 0/ 100% 100%;
}

.li-2 {
    margin-top: 22px;
}

.li-3 {
    margin-top: 23px;
}

.li-4 {
    margin-top: 22px;
}

.y-mark path,
.y-mark line,
.r-mark path,
.r-mark line {
    fill: none;
}

.y-mark text,
.r-mark text {
    font-size: 20px;
    font-family: Microsoft YaHei;
    fill: #a4d5ff;
}


/* 全市交通违规数量同比/环比 */

.car-act {
    width: 1321px;
    height: 430px;
    position: relative;
    background: url(./images/div5.png) 0 0/100% 100%;
    float:right;
    float: right;
}

.card-bg {
    width: 370px;
    top: 26px;
    left: 20px;
}

.icon4 {
    width: 25px;
    height: 25px;
    background: url(images/icon4.png) 0 0/100% 100%;
    position: absolute;
    top: 12px;
    left: 45px;
}

.text-act {
    margin-left: 10px;
}

.penal-symol {
    width: 150px;
    height: 45px;
    font-size: 20px;
    color: #fff;
    text-align: left;
    padding-top: 60px;
    padding-right: 40px;
    display: inline-block;
    cursor: pointer;
    float: right;
}

.tongbi {
    float: left;
    position: relative;
}

.huanbi {
    float: right;
}

.tb-icon {
    width: 50px;
    height: 20px;
    background: url(images/tb.png) 0 0/ 100% 100%;
    position: absolute;
    left: -60px;
}

.hb-icon {
    width: 50px;
    height: 20px;
    background: url(images/hb.png) 0 0/ 100% 100%;
    position: absolute;
    right: 90px;
}

.act-con {
    width: 1400px;
    height: 300px;
    background: url(./images/area-chart-bg.png) no-repeat 90px 0px;
    background-size: 82% 90%;
    position: absolute;
    top:100px;
    left:20px;
}




